<template>
  <div>
    <ptheader class="rpublicity_header">历年奖励情况</ptheader>
  <!--  <textarea id="textareas" class="t" v-model="reward" style="height:25px; overflow:hidden"> </textarea>
    <div class="t" id="divhidden"></div>-->

<!--
    <div contenteditable="true" class="areaDiv" v-text="reward" ></div>-->


    <contentedit   v-model="reward"  class="areaDiv"></contentedit>



    <footer @click="submitBtn">
      提交
    </footer>


  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import contentedit from "@/components/contentedit";
  export default {
    data() {
      return {
        reward: ''
      };
    },
    components: {
      contentedit
    },
    methods:{

      submitBtn() {
        let userId = localStorage.getItem("userId");
        var param = new FormData();
        param.append('userId',userId);
        param.append('reward',this.reward);
        let _this = this;
        this.$http({
          method: 'post',
          url: '/org/user/self/app/update/resume',
          data:param
        }).then( res => {
          Toast("保存成功");
          _this.$router.push('/accounts/index');
        })
      }
    },
    created() {
      let _this = this;
      let userId = localStorage.getItem("userId");
      this.$http.get('/org/user/self/get?id='+userId).then(res => {
        _this.reward = res.reward;
      });
    }
  };
</script>
<style scoped>
  .t {
    width: 96%;
    margin: auto;
    display: block;
    margin-top: 2rem;
  }

  #divhidden {
    visibility: hidden;
  }


  .areaDiv{
    width: 96%;
    margin: auto;
    display: block;
    border:1px solid #dcdcdc;
    min-height: 30rem;
    padding: 1rem;
    margin-top: 0.5rem;
  }
  footer{
    width:96%;
    margin: auto;
    height: 3rem;
    color: white;
    letter-spacing: 0.5rem;
    background: #0b752b;
    line-height: 3rem;
    text-align: center;
    font-size: 16px;
    margin-top: 20px;
    border-radius: 5px;
    margin-bottom: 3rem;
  }
</style>



